* {
  font-family: Georgia, "Times New Roman", Times, serif;
}
body {
  padding: 60px;
}

input {
  width: 100%;
  font-size: 1.2 rem;
  color: gray;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}
.card img {
  max-height: 170px;
}
.card p {
 padding: 5px;
 text-align: center;
}